'use client';

import {
  Cell,
  type PieLabelRenderProps,
  Pie,
  PieChart,
  ResponsiveContainer,
  Legend,
  RadialBarChart,
  RadialBar,
  PolarAngleAxis,
} from 'recharts';
import { Separator } from '@axiom-ui/react/separator';

const RADIAN = Math.PI / 180;

const renderCustomizedNewPacientsLabel = ({
  cx,
  cy,
  midAngle,
  innerRadius,
  outerRadius,
  percent,
}: PieLabelRenderProps) => {
  const radius =
    Number(innerRadius) + (Number(outerRadius) - Number(innerRadius)) * 0.5;
  const x = Number(cx) + radius * Math.cos(-Number(midAngle) * RADIAN);
  const y = Number(cy) + radius * Math.sin(-Number(midAngle) * RADIAN);

  return (
    <text
      x={x}
      y={y}
      fill="white"
      textAnchor={x > Number(cx) ? 'start' : 'end'}
      dominantBaseline="central"
      className="text-[12px]"
    >
      {`${(Number(percent) * 100).toFixed(0)}%`}
    </text>
  );
};

const COLORS = ['#0f6fff', '#62a1ff'];

const data = [
  { name: 'Novos', count: 190 },
  { name: 'Recorrentes', count: 190 },
];

export function PacientsChart() {
  return (
    <div className="flex h-[300px] w-full flex-col items-center gap-4">
      {/* New Pacients Chart */}
      <ResponsiveContainer width="100%" height={180}>
        <PieChart>
          <Pie
            data={data}
            label={renderCustomizedNewPacientsLabel}
            dataKey="count"
            labelLine={false}
          >
            {data.map((_, index) => (
              <Cell
                key={`cell-${index}`}
                fill={COLORS[index % COLORS.length]}
              />
            ))}
          </Pie>
          <Legend iconType="circle" iconSize={8} align="center" />
        </PieChart>
      </ResponsiveContainer>

      {/* Gender Pacients Charts */}
      <div className="flex w-full justify-center gap-4">
        <div className="flex flex-col items-end">
          <ResponsiveContainer height={60}>
            <RadialBarChart
              barSize={60}
              data={[{ value: 80 }]}
              innerRadius="60%"
            >
              <PolarAngleAxis
                type="number"
                domain={[0, 100]}
                angleAxisId={0}
                tick={false}
              />
              <RadialBar
                dataKey="value"
                className="fill-highlight-purple-3"
                background
              />
            </RadialBarChart>
          </ResponsiveContainer>
          <p className="font-medium text-highlight-purple-3">Mulheres</p>
          <p className="text-[14px] text-smooth">Total: 6</p>
        </div>

        <Separator orientation="vertical" className="h-full" />

        <div className="flex flex-col">
          <ResponsiveContainer height={60}>
            <RadialBarChart
              barSize={60}
              data={[{ value: 20 }]}
              innerRadius="60%"
            >
              <PolarAngleAxis
                type="number"
                domain={[0, 100]}
                angleAxisId={0}
                tick={false}
              />
              <RadialBar
                dataKey="value"
                className="fill-highlight-blue-3"
                background
              />
            </RadialBarChart>
          </ResponsiveContainer>
          <p className="font-medium text-highlight-blue-3">Homens</p>
          <p className="text-[14px] text-smooth">Total: 10</p>
        </div>
      </div>
    </div>
  );
}
