'use client';

import {
  Area,
  AreaChart,
  CartesianGrid,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from 'recharts';

export function AgeDistributionChart() {
  const mockData = {
    '12': 22,
    '13': 0,
    '14': 0,
    '15': 1,
    '16': 44,
    '17': 2,
    '18': 0,
    '22': 3,
    '23': 5,
    '24': 0,
    '25': 12,
    '34': 0,
    '35': 45,
    '36': 34,
    '37': 12,
    '43': 3,
  };

  const data = Object.entries(mockData).map(([age, value]) => ({
    name: age,
    value,
  }));

  return (
    <ResponsiveContainer height={300}>
      <AreaChart data={data}>
        <Area dataKey={'value'} dot type="monotone" fillOpacity={0.2} />
        <XAxis dataKey={'name'} tickLine={false} tickMargin={8} />
        <YAxis tickLine={false} tickMargin={8} />
        <Tooltip />
        <CartesianGrid strokeOpacity={0.3} />
      </AreaChart>
    </ResponsiveContainer>
  );
}
