'use client';

import {
  Cell,
  Label,
  Legend,
  Pie,
  PieChart,
  ResponsiveContainer,
  Tooltip,
} from 'recharts';

export function ProceduresChart() {
  const data = [
    { name: 'Consultas', count: 190 },
    { name: 'Exames', count: 120 },
  ];

  const total = data.reduce((acc, entry) => acc + entry.count, 0);

  const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'];

  return (
    <ResponsiveContainer width="100%" height={300}>
      <PieChart>
        <Pie data={data} dataKey="count" fill="#8884d8" innerRadius={65}>
          {data.map((_, index) => (
            <Cell key={`cell-${index}`} fill={COLORS[index] || '#333333'} />
          ))}
          <Label value={total} position="center" fontSize={28} />
        </Pie>
        <Legend align="center" iconType="circle" iconSize={8} />
        <Tooltip />
      </PieChart>
    </ResponsiveContainer>
  );
}
