'use client';

import { dateFormatter } from '@/shared/utils/date-formatter';
import {
  Area,
  AreaChart,
  CartesianGrid,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from 'recharts';

export function PeriodAttendedChart() {
  const mockData = {
    '2023-08-27': 0,
    '2023-08-28': 0,
    '2023-08-29': 20,
    '2023-08-30': 0,
    '2023-08-31': 10,
    '2023-09-01': 0,
    '2023-09-02': 0,
    '2023-09-03': 0,
    '2023-09-04': 0,
    '2023-09-05': 0,
    '2023-09-06': 0,
    '2023-09-07': 4,
    '2023-09-08': 0,
    '2023-09-09': 0,
    '2023-09-10': 0,
    '2023-09-11': 8,
    '2023-09-12': 0,
    '2023-09-13': 0,
    '2023-09-14': 0,
    '2023-09-15': 0,
    '2023-09-16': 22,
    '2023-09-17': 0,
    '2023-09-18': 0,
    '2023-09-19': 11,
    '2023-09-21': 0,
  };

  const data = Object.entries(mockData).map(([date, value]) => ({
    name: date,
    value,
  }));

  return (
    <ResponsiveContainer height={300} width="100%">
      <AreaChart data={data}>
        <Area dataKey={'value'} dot type="monotone" fillOpacity={0.2} />
        <XAxis
          dataKey={'name'}
          tickLine={false}
          tickMargin={8}
          tickFormatter={dateFormatter}
        />
        <YAxis tickLine={false} tickMargin={8} />
        <Tooltip />
        <CartesianGrid strokeOpacity={0.3} />
      </AreaChart>
    </ResponsiveContainer>
  );
}
